
.c-donations-message {
  background-color: $s-color--background-focus;
  display: grid;
  justify-items: center;
  text-align: center;

  &__inner {
    padding: 3.4375rem $s-gutter--edge-mobile 1.875rem;
    width: 99%;

    svg {
      width: 3.125rem;
    }
  }

  &__title {
    max-width: 78.5rem;

    p {
      @include t-font--heading-1;
      margin-bottom: 1rem;
    }

    strong {
      font-weight: normal;
      color: $s-color--font-focus-highlight;
    }
  }


  @include t-media-query($s-medium-up) {
    &__inner {
      padding-top: 8.125rem;
      padding-bottom: 6rem;

      svg {
        width: 4.5%;
      }
    }

    &__title {
      width: 64%;
      margin: 0 auto;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 12.75rem;
      padding-bottom: 8.8125rem;

    }
  }
}